.page-index {
    background: rgb(179, 1, 106);
    display:flex;
    justify-content: space-between;
    align-items:center;
    padding:3.2rem 12rem;
    flex-wrap:wrap;

    @include mq(desktop-wide, min) {
        justify-content: center;
     }

   
    @include mq(desktop, max) {
        padding:3.2rem 8rem;
    }

    @include mq(tablet, max) {
        padding:3.2rem 4rem;
    }

    @include mq(phone, max) {
        padding:3.2rem 2rem;
    }

    div {
        h2,p {
            display: inline-block;
            color:#FFF;
            @extend %reset;
            
        }

        @include mq(desktop-wide, min) {
           flex-basis:65rem;
        }

      

        h2 {
            font-weight: 700;
         

        
        }

        p {
            font-size:1.2rem;
            padding-left:0.3rem;
        }
    }
    
    a{margin-top:0.5rem;}
}

.history {

    padding:0 12rem;
    color: rgb(95, 95, 95);

    @include mq(desktop, max) {
        padding:0 8rem;
    }

    @include mq(tablet, max) {
        padding:0 4rem;
    }

    @include mq(phone, max) {
        padding:0 2rem;
    }

    h2 {
        font-weight:bold;
        @extend %reset;
        padding:5rem 0 1.5rem 0;
    }

    p {
        padding-bottom:4rem;
    }


    img {
        display: block;
        text-align: center;
        margin:0 auto;
        padding-bottom:8rem;

        @include mq (tablet-small ,max) {
            max-width:100%;
            height:auto;

        }
    }
}

.history-heading {
    @include mq (desktop-wide ,min) {
        text-align: left;
        max-width:80rem;
        margin:0 auto;
     
    }
}

